
import {computed, defineComponent} from "vue";

export default defineComponent({
  name: "BtnIcon",
  props:{
    icon:{
      type:String,
      required:true
    },
    title:{
      type:String,
      required:true
    },
    color:{
      type:String,
      required:true
    },
    colorHover:{
      type:String,
      required:true
    },
    href:{
      type:String,
      required:true
    },
  },
  setup(props){
    const colors = computed(() => {
      return {
        '--color'       :   props.color,
        '--colorHover'  :   props.colorHover
      }
    })

    return{
      colors
    }
  }
})
