
import {computed, defineComponent, ref} from "vue";

export default defineComponent({
  name: "BotonHover",
  props:{
    colorHover:{
      type:String,
      required:true
    },
    title:{
      type:String,
      required:true
    },
    redirect:{
      type:String
    },
    isDownload:{
      type:String,
      required:true
    }
  },

  setup(props){
    const colors = computed(() => {
      return {
        '--color' : props.colorHover
      }
    })

    return{
      colors
    }
  }
})
